<template>
  <div class="page-baiduAb">
    <img
      class="img-item"
      v-for="(item, index) in 16"
      :key="index"
      :src="require(`@/assets/baiduAB/${index+1}.jpg`)">
    <WholePage v-if="isWholePage"></WholePage>
    <JumpPage></JumpPage>
  </div>
</template>

<script>
import { throwConfig, throwABKey } from '@/helper/abKeyConfig'
import Store from '@/store'
import JumpPage from './components/JumpPage'
import WholePage from './components/WholePage'
import { isBuyOrder } from '@/helper/payMent'
export default {
  components: { JumpPage, WholePage },
  data () {
    return {
      timer: null // 定时器
    }
  },
  computed: {
    // 根据分组渲染组件
    isWholePage () {
      const { group } = JSON.parse(localStorage.getItem('groupInfo'))
      return group === 'B'
    }
  },
  beforeDestroy () {
    this.timer && clearInterval(this.timer)
  },
  created () {
    this.isBuyOrder()
    this.timer = setInterval(async () => {
      const resultCode = await this.isBuyOrder()
      if (resultCode && resultCode !== 30000) {
        clearInterval(this.timer)
        this.timer = null
      }
    }, 3000)
    window.onpageshow = function (event) {
      if (event.persisted) {
        window.location.reload()
      }
    }
  },
  methods: { isBuyOrder },
  /**
   * @Date: 2021-01-11 18:37:00
   * @author: zjs
   * @description: 判断之前用户分组，没有分组就给他村本地一个
   */
  async beforeRouteEnter (to, form, next) {
    let groupInfo = JSON.parse(localStorage.getItem('groupInfo') || '{}')
    if (!groupInfo.group) {
      const result = await Store.$apis.userOperator.getTestGroup(throwConfig)
      // 默认A组
      result.group = result.group || 'A'
      localStorage.setItem('groupInfo', JSON.stringify(result))
      const { group, uniqFlag } = result
      const throwOtherLog = JSON.stringify({
        ab_key: throwABKey,
        ab_group: group,
        ab_uniq_user: uniqFlag
      })
      localStorage.setItem('throwOtherLog', throwOtherLog)
    }
    Store.$log.update({
      alternate: localStorage.getItem('throwOtherLog')
    })
    groupInfo = JSON.parse(localStorage.getItem('groupInfo') || '{}')
    // 埋点
    const logShowConfig = {
      B: '106_toufang_lcx_0093',
      C: '106_toufang_lcx_0103'
    }
    // 如果是A组跳转页面
    if (groupInfo.group === 'A') {
      return next({ name: 'BaiduABOriginalPage', query: to.query })
    }
    // BC埋点
    Store.$log.show({
      current_page: location.href,
      module_ori: '基于整体页面',
      module_part: logShowConfig[groupInfo.group]
    })
    next()
  }
}
</script>

<style lang="scss" >
.page-baiduAb {
  width: 100%;
  background-color: #f8e1bd;
  padding-bottom: 140px;

  img {
    vertical-align: middle;
  }

  .img-item {
    max-width: 100%;
    vertical-align: middle;
  }
}
</style>
