<template>
  <div class="page-jumpPage">
    <div class="content-main">
      <div class="text__box">
        <span class="text__money">￥12</span>
        <!-- <span class="text__original">市场价￥199</span> -->
        <!-- <div class="content-desc">今日仅限100个抢购名额</div> -->
      </div>
      <div class="submit-btn" @click="goBaiduABPay">限量抢购</div>
    </div>
  </div>
</template>

<script>
import { getSearch } from '@/utils'
import { jumpLogConfig } from '../config'
import vue from '@/utils/busVue'
import { clickLog } from '@/helper/clickLog'

export default {
  name: 'JumpPage',
  data () {
    return {
      ...jumpLogConfig // 埋点数据
    }
  },
  methods: {
    /**
     * @Date: 2020-12-29 17:47:18
     * @author: zjs
     * @description: 去百度ab测试支付页
     */
    goBaiduABPay () {
      const { group } = JSON.parse(localStorage.getItem('groupInfo'))
      if (group === 'C') {
        clickLog({
          module_ori: '底部悬浮栏',
          module_part: this.BOTTOM_BTN
        })
      }
      // 需要跳转到底部
      if (group === 'B') {
        vue.$emit('focusWholePageInput')
        return
      }
      const id = getSearch('id')
      this.$router.push({
        name: 'BaiduABPay',
        query: {
          ...this.$route.query,
          id
        }
      })
    }
  }
}
</script>
