<template>
  <div class="page-wholePage">
    <div class="content">
      <div class="mt__30 content-title">
        - 开课3天内 对课程不满意 无条件退款 -
      </div>
      <!-- 手机号 -->
      <div class="form-item mt__40">
        <p class="ml__10 mr__20">+86</p>
        <input
          ref="focusInput"
          placeholder="请输入手机号"
          class="form-line"
          v-model.trim="phone" />
      </div>
      <!-- 验证码 -->
      <div class="form-item mt__10">
        <input
          placeholder="请输入验证码"
          v-model.trim="code" />
        <CountDownBtn
          class="pl__20 phone-code form-line"
          :phone="phone"
          :get-code-success-back="getCodeSuccessBack"
          :get-code-error-back="getCodeErrorBack"></CountDownBtn>
      </div>
      <!-- 支付按钮 -->
      <div class="submit-btn mt__30" @click="pay">限量抢购</div>
      <div class="btn-text mt__30">
        *注：抢购后，班主任将全程指导学习
      </div>
      <!-- 今日仅限100抢购名额 -->
    </div>
    <!-- 挽留弹窗 -->
    <BackDialog
      @backDiaLogSubmit="backDiaLogSubmit"></BackDialog>
    <!-- 支付类型弹窗 -->
    <PayTypeDialog
      @clickCloseIcon="closePayTypeDialog"
      @payTypeClick="payLogAndSubmitPay"
      :pay-dialog-config="payDialogConfig"></PayTypeDialog>
  </div>
</template>

<script>
import PayTypeDialog from '@/components/BaiduABBusniess/payTypeDialog'
import CountDownBtn from '@/components/CountDownBtn'
import BackDialog from '@/components/BaiduABBusniess/backDialog'
import { getPayQuery, paySuccessBack, payErrorBack, getCodeSuccessBack, getCodeErrorBack } from '@/helper/baiduABPayMentBack'
import { wholeLogConfig } from '../config'
import { openPayTypeDialog, payTypeClick, closePayTypeDialog } from '@/helper/payTypeDialog'
import vue from '@/utils/busVue'
import { Dialog } from 'vant'
import { initPhoneForm } from '@/helper/backDiaLog'
import { submitPay, isBuyOrder } from '@/helper/payMent'
import { clickLog } from '@/helper/clickLog'
import { isPhone } from '@/utils'

export default {
  name: 'WholePage',
  components: { PayTypeDialog, CountDownBtn, BackDialog },
  data () {
    return {
      phone: '', // 手机号
      code: '', // 验证码
      paytype: 2, // 支付类型
      successPag: 'Officals', // 支付后要跳转的成功页面
      payDialogConfig: {
        dialogMark: false
      }, // 支付弹窗
      ...wholeLogConfig // 埋点字段
    }
  },
  beforeDestroy () {
    vue.$off('focusWholePageInput')
  },
  created () {
    this.initPhoneForm()
    vue.$on('focusWholePageInput', () => {
      this.pay()
    })
  },
  methods: {
    isBuyOrder,
    openPayTypeDialog,
    payTypeClick,
    closePayTypeDialog,
    initPhoneForm,
    submitPay,
    paySuccessBack,
    payErrorBack,
    getCodeSuccessBack,
    getCodeErrorBack,
    getPayQuery,
    /**
     * @Date: 2021-01-14 15:03:00
     * @author: zjs
     * @description: 回调弹窗继续支付
     */
    backDiaLogSubmit () {
      const otherQuery = JSON.parse(localStorage.getItem('lastPayQuery'))
      this.submitPay({
        apiQuery: this.getPayQuery({
          ...otherQuery,
          return_url: encodeURIComponent(location.href)
        }),
        successBack: this.paySuccessBack,
        errorBack: this.payErrorBack
      })
    },
    /**
     * @Date: 2021-01-14 14:40:47
     * @author: zjs
     * @description: 支付类型埋点和支付
     */
    payLogAndSubmitPay (paytype) {
      this.payTypeClick(paytype)
      this.submitPay({
        apiQuery: this.getPayQuery({
          pay_type: paytype,
          return_url: encodeURIComponent(location.href)
        }),
        successBack: this.paySuccessBack,
        errorBack: this.payErrorBack
      })
    },
    pay () {
      clickLog({
        module_ori: '底部悬浮栏',
        module_part: this.BOTTOM_BTN
      })
      const { phone } = this
      const msg = phone ? '手机号码有误，请重填' : '请填写手机号'
      if (!isPhone(phone)) {
        Dialog({ message: msg }).then(() => {
          this.$refs.focusInput.focus()
          clickLog({
            module_ori: '表单错误模块',
            module_part: this.ERROR_PHONE
          })
        })
      }
      this.isBuyOrder(phone).then(res => {
        this.openPayTypeDialog()
      })
    }
  }
}
</script>
<style lang="scss">
.page-wholePage {
  width: 100%;
  padding-bottom: 30px;
  background: #f8e1bd;
  @include horVerCenter;
  .content {
    width: calc(100% - 58px);
    min-height: 410px;
    padding-bottom: 30px;
    background: #fbe2da;
    border: 16px solid #ffffff;
    @include verCenter;
    flex-direction: column;
    .content-title {
      font-size: 32px;
      font-weight: 500;
      color: #333333;
      text-align: center;
    }
    .form-item {
      width: calc(100% - 72px);
      height: 80px;
      border: 3px solid #704f40;
      background-color: #fff;
      font-size: 32px;
      font-weight: 400;
      color: #929292;
      display: flex;
      * {
        height: 100%;
      }
      p {
        @include horVerCenter;
      }
      .phone-code {
        font-size: 29px;
        font-weight: 400;
        color: #b92525;
      }
      input {
        background-color: transparent;
        border: 0;
        padding-left: 30px;
        width: calc(100% - 230px);
        border-radius: 0;
        &::placeholder {
          color: #929292;
          font-size: 32px;
          font-weight: 400;
        }
      }
      .form-line {
        border-radius: 0;
        border-left: 3px solid #704f40;
      }
    }
    .submit-btn {
      @include horVerCenter;
      width: calc(100% - 72px);
      height: 90px;
      background: #fd2706;
      font-size: 48px;
      font-weight: bold;
      color: #ffffff;
    }
    .btn-text {
      color: #595959;
      line-height: 37px;
      text-align: center;
      max-width: 500px;
      width: 446px;
      font-size: 27px;
      font-weight: 400;
    }
  }
}
</style>
